<p><code v-pre="">vc-map-overview</code> 组件用于加载鹰眼部件。<strong>注意：</strong> 使用此组件需要引入 <code v-pre="">import 'vue-cesium/lib/vc-map-overview.css'</code>。此组件默认没有引入，需要单独引入。</p>
<pre v-pre=""><code v-pre="" class="language-js"><span class="hljs-keyword">import</span> { <span class="hljs-title class_">VueCesium</span>, <span class="hljs-title class_">VcOverviewMap</span> } <span class="hljs-keyword">from</span> <span class="hljs-string">'vue-cesium'</span>
<span class="hljs-title class_">Vue</span>.<span class="hljs-title function_">use</span>(<span class="hljs-title class_">VueCesium</span>, options)
<span class="hljs-title class_">Vue</span>.<span class="hljs-title function_">use</span>(<span class="hljs-title class_">VcOverviewMap</span>)
<span class="hljs-keyword">import</span> <span class="hljs-string">'vue-cesium/lib/vc-map-overview.css'</span>
</code></pre>
<h2>示例</h2>
<h3>加载鹰眼部件</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer :timeline="timeline" @ready="ready" scene-mode-picker="">
        <vc-map-overview ref="map"></vc-map-overview>
        <vc-layer-imagery>
          <vc-provider-imagery-openstreetmap></vc-provider-imagery-openstreetmap>
        </vc-layer-imagery>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
