    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">:timeline</span>=<span class="hljs-string">"timeline"</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-map-overview</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-map-overview</span>&gt;</span>
