<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String</td>
<td><code v-pre="">'https://webst01.is.autonavi.com/appmaptile?style=7&amp;x={x}&amp;y={y}&amp;z={z}'</code></td>
<td><code v-pre="">optional</code> 指定鹰眼加载的地图 url。</td>
</tr>
<tr>
<td>width</td>
<td>Number</td>
<td><code v-pre="">150</code></td>
<td><code v-pre="">optional</code> 指定鹰眼控件宽度。</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre="">150</code></td>
<td><code v-pre="">optional</code> 指定鹰眼控件高度。</td>
</tr>
<tr>
<td>anchor</td>
<td>String</td>
<td><code v-pre="">'bottomright'</code></td>
<td><code v-pre="">optional</code> 指定鹰眼位置。 <code v-pre="">topleft</code>、<code v-pre="">topright</code>、<code v-pre="">bottomleft</code>、<code v-pre="">bottomright</code></td>
</tr>
<tr>
<td>aimingRectOptions</td>
<td>Object</td>
<td><code v-pre="">{ color: '#ff1100', weight: 3 }</code></td>
<td><code v-pre="">optional</code> 指定鹰眼表示当前地图范围矩形参数。</td>
</tr>
<tr>
<td>shadowRectOptions</td>
<td>Object</td>
<td><code v-pre="">{ color: '#0000AA', weight: 1, opacity: 0, fillOpacity: 0 }</code></td>
<td><code v-pre="">optional</code> 指定鹰眼地图蒙版参数。</td>
</tr>
<tr>
<td>toggleDisplay</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定鹰眼收缩按钮是否可见。</td>
</tr>
</tbody>
</table>
<hr>
